// Browser/Node.js compatibility
import { EventEmitter } from 'eventemitter3';
import * as ws from 'ws';

const isWindow =
  typeof globalThis !== 'undefined' &&
  typeof (globalThis as any).window !== 'undefined';
const WebSocketImpl = isWindow ? (globalThis as any).WebSocket : ws.WebSocket;

import {
  BaseWebSocketMessage,
  ErrorResponse,
} from '@dynamic-labs-wallet/forward-mpc-shared';
import {
  HandshakeV1RequestMessage,
  SignMessageV1RequestMessage,
  messageRegistry,
  HashAlgorithm,
  generateMlKem768Keypair,
  decapsulateMlKem768,
  encryptKeyshare,
  type SigningAlgorithmName,
} from '@dynamic-labs-wallet/forward-mpc-shared';
import { NitroAttestationVerifier } from './attestation';
import { AttestationVerificationConfig } from './attestation/types';
import { sha256 } from '@noble/hashes/sha2.js';
import { hexToBytes, randomBytes } from '@noble/hashes/utils.js';
import { either } from 'fp-ts';
import { SigningAlgorithm } from '@dynamic-labs-wallet/core';

export interface ForwardMPCClientOptions {
  reconnectAttempts?: number;
  reconnectInterval?: number;
  connectionTimeout?: number;
  heartbeatInterval?: number;
  attestationConfig?: AttestationVerificationConfig;
  attestationVerifier?: any; // Allow custom verifier (mock or real)
  bypassAttestation?: boolean; // Default: false. Only for testing!
}

export interface ClientEvents {
  connected: () => void;
  disconnected: () => void;
  error: (error: Error) => void;
  message: (message: BaseWebSocketMessage) => void;
}

export class ForwardMPCClient extends EventEmitter {
  private ws: any = null;
  private url: string;
  private options: Required<
    Omit<ForwardMPCClientOptions, 'attestationVerifier' | 'attestationConfig'>
  > & {
    attestationVerifier?: any;
    attestationConfig?: AttestationVerificationConfig;
  };
  private attestationVerifier: any | null = null; // Can be NitroAttestationVerifier or mock
  private isConnected = false;
  private messageId = 0;
  private pendingRequests = new Map<
    string,
    {
      resolve: (value: any) => void;
      reject: (error: Error) => void;
      timeout: NodeJS.Timeout;
    }
  >();
  private sharedSecret: Uint8Array | null = null;
  private decapsulationKey: Uint8Array | null = null;
  private connectionId: string;
  private handshakeNonce: Uint8Array | null = null;
  private attestationPromise: Promise<void> | null = null;

  constructor(url: string, options: ForwardMPCClientOptions = {}) {
    super();

    this.url = url;
    this.connectionId = ''; // Will be set by server during handshake
    this.options = {
      reconnectAttempts: options.reconnectAttempts ?? 3,
      reconnectInterval: options.reconnectInterval ?? 1000,
      connectionTimeout: options.connectionTimeout ?? 10000,
      heartbeatInterval: options.heartbeatInterval ?? 30000,
      attestationConfig: options.attestationConfig,
      attestationVerifier: options.attestationVerifier,
      bypassAttestation: options.bypassAttestation ?? false,
    };

    // Initialize attestation verifier - use custom verifier if provided, otherwise create from config
    if (options.attestationVerifier) {
      this.attestationVerifier = options.attestationVerifier;
    } else if (this.options.attestationConfig) {
      this.attestationVerifier = new NitroAttestationVerifier(
        this.options.attestationConfig
      );
    }
  }

  async connect(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (this.isConnected) {
        resolve();
        return;
      }

      const timeout = setTimeout(() => {
        reject(new Error('Connection timeout'));
      }, this.options.connectionTimeout);

      this.ws = new WebSocketImpl(this.url);

      // Handle both browser and Node.js WebSocket APIs
      if (isWindow) {
        // Browser WebSocket API
        this.ws.onopen = () => {
          clearTimeout(timeout);
          this.isConnected = true;
          this.emit('connected');
          resolve();
        };

        this.ws.onerror = (_error: any) => {
          clearTimeout(timeout);
          this.emit('error', new Error('WebSocket error'));
          reject(new Error('WebSocket error'));
        };

        this.ws.onmessage = (event: any) => {
          this.handleMessage(event.data);
        };

        this.ws.onclose = () => {
          this.isConnected = false;
          this.emit('disconnected');
          this.cleanup();
        };
      } else {
        // Node.js WebSocket API
        this.ws.on('open', () => {
          clearTimeout(timeout);
          this.isConnected = true;
          this.emit('connected');
          resolve();
        });

        this.ws.on('error', (error: any) => {
          clearTimeout(timeout);
          this.emit('error', error);
          reject(error);
        });

        this.ws.on('message', (data: any) => {
          this.handleMessage(data.toString());
        });

        this.ws.on('close', () => {
          this.isConnected = false;
          this.emit('disconnected');
          this.cleanup();
        });
      }
    });
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
    this.cleanup();
  }

  private cleanup(): void {
    // Reject all pending requests
    for (const [, request] of this.pendingRequests) {
      clearTimeout(request.timeout);
      request.reject(new Error('Connection closed'));
    }
    this.pendingRequests.clear();

    // Clear sensitive cryptographic material
    if (this.sharedSecret) {
      this.sharedSecret.fill(0);
      this.sharedSecret = null;
    }
    if (this.decapsulationKey) {
      this.decapsulationKey.fill(0);
      this.decapsulationKey = null;
    }
    if (this.handshakeNonce) {
      this.handshakeNonce.fill(0);
      this.handshakeNonce = null;
    }

    // Clear connection ID
    this.connectionId = '';

    // Reset attestation state
    this.attestationPromise = null;
  }

  /**
   * Ensures the WebSocket connection is active and handshake is completed.
   * Automatically connects and performs handshake if needed.
   * @returns Object containing the shared secret and connection ID
   * @throws Error if connection or handshake fails
   */
  async ensureWsConnection(): Promise<{
    sharedSecret: Uint8Array;
    connectionId: string;
  }> {
    // Check if we need to connect
    if (!this.isConnected || !this.ws) {
      await this.connect();
    }

    // Check if we need to perform handshake
    if (!this.sharedSecret || !this.connectionId) {
      await this.handshake();
    }

    // Defensive check: if attestation verifier is configured, promise must exist
    if (
      this.attestationVerifier &&
      !this.options.bypassAttestation &&
      !this.attestationPromise
    ) {
      throw new Error(
        'Attestation verifier configured but attestation was not performed'
      );
    }

    // Await attestation completion (throws if failed)
    if (this.attestationPromise) {
      try {
        await this.attestationPromise;
      } catch (error) {
        // Handle attestation failure by disconnecting
        // Side effect is here in the caller's context, not in the promise
        this.disconnect();
        throw error; // Re-throw for the caller
      }
    }

    // Final validation - should never happen but defensive check
    if (!this.sharedSecret || !this.connectionId) {
      throw new Error('Failed to establish connection and handshake');
    }

    return {
      sharedSecret: this.sharedSecret,
      connectionId: this.connectionId,
    };
  }

  private handleMessage(data: string): void {
    try {
      // Parse the raw message first to extract requestId
      const parsed = JSON.parse(data);
      const requestId = parsed.requestId;

      // Remove requestId from the message before deserializing
      if (requestId) {
        delete parsed.requestId;
      }

      // Now deserialize the message without requestId
      const message = this.deserializeMessage(JSON.stringify(parsed));

      // Check if this is a response to a pending request
      if (requestId && this.pendingRequests.has(requestId)) {
        const request = this.pendingRequests.get(requestId)!;
        this.pendingRequests.delete(requestId);
        clearTimeout(request.timeout);

        // Handle unified response format with embedded errors
        if (message.type === 'error') {
          // Legacy error response format
          const errorMsg = message as ErrorResponse;
          request.reject(new Error(errorMsg.error.message));
        } else if (message.type.endsWith('_response')) {
          // Check if the response contains an error field
          const responseData = (message as any).getData
            ? (message as any).getData()
            : message;
          if (responseData.error) {
            request.reject(new Error(responseData.error.message));
          } else {
            request.resolve(message);
          }
        } else {
          request.resolve(message);
        }
        return;
      }

      this.emit('message', message);
    } catch (error) {
      this.emit('error', new Error(`Failed to parse message: ${error}`));
    }
  }

  private sendRequest<T>(message: any): Promise<T> {
    return new Promise((resolve, reject) => {
      if (!this.isConnected || !this.ws) {
        reject(new Error('Not connected'));
        return;
      }

      const requestId = `req_${++this.messageId}_${Date.now()}`;
      // Add requestId to message (works for both objects and Message instances)
      const messageWithId = message;
      messageWithId.requestId = requestId;

      const timeout = setTimeout(() => {
        this.pendingRequests.delete(requestId);
        reject(new Error('Request timeout'));
      }, 30000); // 30 second timeout for attestation verification

      this.pendingRequests.set(requestId, { resolve, reject, timeout });

      try {
        // Use custom serializer that encodes using io-ts schemas
        const serializedMessage = this.serializeMessage(messageWithId);
        this.ws.send(serializedMessage);
      } catch (error) {
        this.pendingRequests.delete(requestId);
        clearTimeout(timeout);
        reject(error);
      }
    });
  }

  private serializeMessage(message: any): string {
    try {
      // If it's a Message class instance, use its encode method
      if (message && typeof message.encode === 'function') {
        const encoded = message.encode();
        // Add requestId if present
        if (message.requestId) {
          encoded.requestId = message.requestId;
        }
        return JSON.stringify(encoded);
      }

      // Fallback to regular JSON for other objects
      return JSON.stringify(message);
    } catch (error) {
      throw new Error(
        `Failed to serialize message: ${
          error instanceof Error ? error.message : String(error)
        }`
      );
    }
  }

  private deserializeMessage(data: string): any {
    try {
      const parsed = JSON.parse(data);

      // Check if messageRegistry exists and has decode method
      if (!messageRegistry) {
        throw new Error('messageRegistry is undefined');
      }

      if (typeof messageRegistry.decode !== 'function') {
        throw new Error('messageRegistry.decode is not a function');
      }

      // Use message registry to decode
      const result = messageRegistry.decode(parsed);
      if (either.isLeft(result)) {
        // If registry can't decode it, return as-is (might be error response, etc.)
        return parsed;
      }

      return result.right;
    } catch (error) {
      throw new Error(
        `Failed to deserialize message: ${
          error instanceof Error ? error.message : String(error)
        }`
      );
    }
  }

  async handshake(): Promise<any> {
    // Generate ML-KEM-768 keypair and use encapsulation key as challenge
    const { encapsulationKey, decapsulationKey } = generateMlKem768Keypair();

    // Store decapsulation key for later use
    this.decapsulationKey = decapsulationKey;

    // Generate a cryptographically secure nonce for attestation
    const nonceBytes = randomBytes(32); // 256-bit nonce

    // Store original bytes for verification
    this.handshakeNonce = nonceBytes;

    const request = new HandshakeV1RequestMessage({
      challenge: encapsulationKey, // ML-KEM-768 encapsulation key (will be encoded to hex by the message class)
      nonce: nonceBytes, // User data nonce for attestation document
    });

    const response = await this.sendRequest<any>(request);

    // Extract shared secret from response using ML-KEM decapsulation
    const responseData = response.getData();
    if (
      responseData.encapsulatedSharedSecret &&
      responseData.connectionId &&
      this.decapsulationKey
    ) {
      // Use the server's connection ID for consistent key derivation
      this.connectionId = responseData.connectionId;

      // Convert hex string back to bytes for decapsulation
      const cipherText = hexToBytes(responseData.encapsulatedSharedSecret);

      // Perform ML-KEM decapsulation to derive shared secret
      this.sharedSecret = decapsulateMlKem768(
        this.decapsulationKey,
        cipherText
      );
    }

    // Verify attestation document (store promise for later await)
    if (
      this.attestationVerifier &&
      responseData.attestationDoc &&
      responseData.encapsulatedSharedSecret &&
      !this.options.bypassAttestation
    ) {
      // Convert hex string back to bytes for challenge verification
      const cipherText = hexToBytes(responseData.encapsulatedSharedSecret);

      // Store promise - method already throws on failure
      this.attestationPromise = this.verifyAttestationDocument(
        responseData.attestationDoc,
        cipherText
      );
    }

    return response;
  }

  async signMessage(params: {
    keyshare: any; // Keyshare object from Dynamic Wallet SDK
    message: Uint8Array | string; // Message to sign (supports hex string or Uint8Array)
    relayDomain: string;
    signingAlgo: SigningAlgorithm;
    hashAlgo?: HashAlgorithm;
    derivationPath?: Uint32Array;
    tweak?: Uint8Array;
    roomUuid: string; // MPC room UUID for signing ceremony
  }): Promise<any> {
    // Ensure connection is active and handshake is completed (auto-connect if needed)
    const { sharedSecret, connectionId } = await this.ensureWsConnection();

    // Convert hex string to Uint8Array if needed
    let messageToSign: Uint8Array;
    if (typeof params.message === 'string') {
      // Handle hex strings (with or without 0x prefix)
      const cleanHex = params.message.startsWith('0x')
        ? params.message.slice(2)
        : params.message;
      messageToSign = hexToBytes(cleanHex);
    } else {
      // Already a Uint8Array
      messageToSign = params.message;
    }

    // Encrypt the keyshare using AES-256-GCM
    const encryptedKeyshare = await encryptKeyshare(
      params.keyshare,
      sharedSecret,
      connectionId,
      params.signingAlgo as unknown as SigningAlgorithmName
    );

    const request = new SignMessageV1RequestMessage({
      relayDomain: params.relayDomain,
      signingAlgo: params.signingAlgo,
      hashAlgo: params.hashAlgo,
      derivationPath: params.derivationPath,
      tweak: params.tweak,
      keyshare: encryptedKeyshare,
      message: messageToSign, // Always pass Uint8Array to the message class
      roomUuid: params.roomUuid,
    });

    return this.sendRequest<any>(request);
  }

  get connected(): boolean {
    return this.isConnected;
  }

  /**
   * Generate a unique connection ID
   */

  /**
   * Verify attestation document from handshake response
   * Uses base64-encoded attestation document directly
   */
  private async verifyAttestationDocument(
    attestationDocBase64: string,
    cipherText: Uint8Array
  ): Promise<void> {
    if (!this.attestationVerifier) {
      return; // No verifier configured
    }

    try {
      // Generate the expected challenge (hash of cipher text from ML-KEM encapsulation)
      const challengeHash = sha256(cipherText);
      const expectedChallenge = Array.from(challengeHash)
        .map((b: number) => b.toString(16).padStart(2, '0'))
        .join('');

      // Verify the attestation document directly with base64 string (no conversion needed!)
      // Nonce is required for security (prevents replay attacks)
      if (!this.handshakeNonce) {
        throw new Error(
          'Nonce not found - handshake may not have completed properly'
        );
      }

      const result = await this.attestationVerifier.verify(
        attestationDocBase64,
        expectedChallenge,
        this.handshakeNonce
      );

      if (!result.valid) {
        const errorMsg = `Attestation verification failed: ${result.errors.join(
          ', '
        )}`;
        this.emit('error', new Error(errorMsg));
        throw new Error(errorMsg);
      }

      // Emit success event with verification result
      this.emit('message', {
        type: 'attestation-verified',
        timestamp: Date.now(),
        data: result,
      } as any);
    } catch (error) {
      const errorMsg = `Attestation verification error: ${
        error instanceof Error ? error.message : String(error)
      }`;
      this.emit('error', new Error(errorMsg));
      throw new Error(errorMsg);
    }
  }
}
