import {
  AttestationVerificationResult,
  AttestationVerificationConfig,
} from './types';
import { ATTESTATION_CONSTANTS } from './constants';

// Import Evervault WASM attestation bindings
import init, {
  validateAttestationDocPcrs,
  PCRs,
  getUserData,
  getNonce,
} from '@evervault/wasm-attestation-bindings';

/**
 * Nitro Enclave Attestation Document Verifier
 * Uses Evervault's official WASM attestation bindings
 * Optimized for client-side usage with hex string input
 */
export class NitroAttestationVerifier {
  private readonly config: AttestationVerificationConfig;
  private wasmInitialized = false;

  constructor(config: AttestationVerificationConfig) {
    this.config = {
      strictCertValidation: true,
      maxAge: ATTESTATION_CONSTANTS.MAX_ATTESTATION_AGE,
      ...config,
    };
  }

  /**
   * Initialize WASM module if not already initialized
   */
  private async ensureWasmInitialized(): Promise<void> {
    if (!this.wasmInitialized) {
      try {
        // Initialize WASM module - in browser, this will automatically load the .wasm file
        await init();
        this.wasmInitialized = true;
      } catch (error) {
        throw new Error(
          `Failed to initialize WASM module: ${error instanceof Error ? error.message : 'Unknown error'}`
        );
      }
    }
  }

  /**
   * Verify an attestation document using Evervault WASM bindings
   * Accepts base64-encoded attestation document directly
   *
   * @param attestationDocBase64 - Base64-encoded attestation document
   * @param expectedChallenge - Expected challenge (ciphertext hash)
   * @param expectedNonce - Expected nonce (REQUIRED for security)
   */
  async verify(
    attestationDocBase64: string,
    expectedChallenge: string,
    expectedNonce: Uint8Array
  ): Promise<AttestationVerificationResult> {
    try {
      // Ensure WASM module is initialized
      await this.ensureWasmInitialized();

      // Create PCRs container with expected values
      const expectedPcrs = PCRs.empty();
      expectedPcrs.pcr8 = this.config.expectedPcr8;

      // Use Evervault WASM bindings to verify the attestation document (already in base64)
      const isValid = validateAttestationDocPcrs(attestationDocBase64, [
        expectedPcrs,
      ]);

      if (!isValid) {
        return {
          valid: false,
          errors: ['Attestation document PCR verification failed'],
          timestamp: Date.now(),
        };
      }

      // Verify challenge (ciphertext hash) if provided
      if (expectedChallenge) {
        try {
          const userData = getUserData(attestationDocBase64);
          if (!userData) {
            return {
              valid: false,
              errors: ['No user data found in attestation document'],
              timestamp: Date.now(),
            };
          }

          // Convert user data to string and verify it contains the expected challenge
          const userDataString = new TextDecoder('utf-8').decode(userData);

          // The challenge should be the hex-encoded hash of the ciphertext
          // Verify that the attestation document's user data contains this challenge
          if (!userDataString.includes(expectedChallenge)) {
            return {
              valid: false,
              errors: [
                'Ciphertext hash verification failed - challenge not found in attestation user data',
              ],
              timestamp: Date.now(),
            };
          }
        } catch (error) {
          return {
            valid: false,
            errors: [
              `Failed to extract or verify ciphertext hash: ${error instanceof Error ? error.message : String(error)}`,
            ],
            timestamp: Date.now(),
          };
        }
      }

      // Verify nonce (REQUIRED for security - prevents replay attacks)
      try {
        // Use WASM getNonce function to extract nonce from attestation document
        const extractedNonceRaw = getNonce(attestationDocBase64);

        if (!extractedNonceRaw) {
          return {
            valid: false,
            errors: ['No nonce found in attestation document'],
            timestamp: Date.now(),
          };
        }

        // The attestation document stores the nonce as a base64-encoded string
        // We need to decode it back to the original bytes
        let extractedNonce: Uint8Array;
        try {
          // Convert to string and decode from base64 (cross-platform)
          const nonceString = new TextDecoder().decode(extractedNonceRaw);

          // Browser-compatible base64 decoding
          if (typeof atob !== 'undefined') {
            // Browser environment
            const binaryString = atob(nonceString);
            extractedNonce = new Uint8Array(binaryString.length);
            for (let i = 0; i < binaryString.length; i++) {
              extractedNonce[i] = binaryString.charCodeAt(i);
            }
          } else {
            // Node.js environment
            const decodedBuffer = Buffer.from(nonceString, 'base64');
            extractedNonce = new Uint8Array(decodedBuffer);
          }
        } catch (decodeError) {
          return {
            valid: false,
            errors: [
              `Failed to decode nonce from base64: ${decodeError instanceof Error ? decodeError.message : String(decodeError)}`,
            ],
            timestamp: Date.now(),
          };
        }

        // Compare nonces byte-for-byte
        if (extractedNonce.length !== expectedNonce.length) {
          return {
            valid: false,
            errors: [
              `Nonce length mismatch: expected ${expectedNonce.length} bytes, got ${extractedNonce.length} bytes`,
            ],
            timestamp: Date.now(),
          };
        }

        for (let i = 0; i < expectedNonce.length; i++) {
          if (extractedNonce[i] !== expectedNonce[i]) {
            return {
              valid: false,
              errors: ['Nonce verification failed - nonce mismatch'],
              timestamp: Date.now(),
            };
          }
        }
      } catch (error) {
        return {
          valid: false,
          errors: [
            `Failed to extract or verify nonce: ${error instanceof Error ? error.message : String(error)}`,
          ],
          timestamp: Date.now(),
        };
      }

      return {
        valid: true,
        errors: [],
        timestamp: Date.now(),
      };
    } catch (error) {
      return {
        valid: false,
        errors: [
          `Attestation verification error: ${error instanceof Error ? error.message : String(error)}`,
        ],
        timestamp: Date.now(),
      };
    }
  }
}
